import React, { useEffect } from 'react';
import './App.css';
import LoginPage from './pages/LoginPage';
import SignupPage from './pages/SignupPage';
import Home from './pages/Home';
import CartPage from './pages/CartPage';
import Checkout from './pages/Checkout';
import ProductDetailsPage from './pages/ProductDetailsPage';
import NotFound from './pages/NotFound';
import { BrowserRouter,Routes,Route } from 'react-router-dom';
import Protected from './features/auth/Protected';
import { useDispatch, useSelector } from 'react-redux';
import { selectLoggedInUser } from './features/auth/AuthSlice';
import { fetchItemsByUserIdAsync } from './features/cart/cartSlice';
import OrderSuccessPage from './pages/OrderSuccessPage';
import MyOrdersPage from './pages/MyOrdersPage';
import { fetchLoggedInUserAsync } from './features/user/userSlice';
import UserProfilePage from './pages/UserProfilePage';
import Logout from './features/auth/Logout';
import ProtectedAdmin from './features/auth/ProtectedAdmin';
import AdminHome from './pages/AdminHome';
import AdminProductFormPage from './pages/AdminProductForm';
import AdminOrdersPage from './pages/AdminOrdersPage';


function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);

  useEffect(()=>{
    if(user){
      dispatch(fetchItemsByUserIdAsync(user.id))
      dispatch(fetchLoggedInUserAsync(user.id))
    }
  }, [dispatch,user])
  

  return (
    <div className="App">
    <BrowserRouter>
      <Routes>
        <Route path='/' element={<Home/>}></Route>
        <Route path='/login' element={<LoginPage/>}></Route>
        <Route path='/signup' element={<SignupPage/>}></Route>
        <Route path='/cart' element={<Protected><CartPage/></Protected>}></Route>
        <Route path='/checkout' element={<Protected><Checkout/></Protected>}></Route>
        <Route path='/productdetails/:id' element={<ProductDetailsPage/>}></Route>
        <Route path='/order-success/:id' element={<OrderSuccessPage/>}></Route>
        <Route path='/myorders' element={<Protected><MyOrdersPage/></Protected>}></Route>
        <Route path='/myprofile' element={<Protected><UserProfilePage/></Protected>}></Route>
        <Route path='/logout' element={<Protected><Logout/></Protected>}></Route>

        {/* routes for admin */}
        <Route path='/admin' element={<ProtectedAdmin><AdminHome/></ProtectedAdmin>}></Route>
        <Route path='/admin/product-form' element={<ProtectedAdmin><AdminProductFormPage/></ProtectedAdmin>}></Route>
        <Route path='/admin/product-form/edit/:id' element={<ProtectedAdmin><AdminProductFormPage/></ProtectedAdmin>}></Route>
        <Route path='/admin/orders' element={<ProtectedAdmin><AdminOrdersPage/></ProtectedAdmin>}></Route>

        <Route path="*" element={<NotFound/>}> </Route>
      </Routes>
    </BrowserRouter>
    </div>
  );
}

export default App;
